@import 'https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css';
@import 'bootstrap/bssbootstrap_pc.css';
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
:disabled,
[disabled],
[aria-disabled="true"] {
  cursor: not-allowed;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.svg-sprite {
  display: none;
}
.vertical-center {
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.vertical-center.container--main {
  height: calc(100vh - 62px);
}
.vertical-center > .row {
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}
@media (max-width: 540px) {
  .vertical-center {
    min-height: auto;
    display: block;
  }
  .vertical-center.container--main {
    height: auto;
  }
  .vertical-center >.row {
    width: auto;
    margin-right: -15px;
    margin-left: -15px;
  }
}
