.mt-20 {
  margin-top: 20px;
}
.mt-10 {
  margin-top: 10px;
}
.m-auto {
  margin-left: auto;
  margin-right: auto;
}
.checkbox .cr,
.radio .cr {
  position: relative;
  display: inline-block;
  border: 1px solid #000;
  background: #d3d3d3;
  border-radius: 5px;
  width: 17px;
  height: 17px;
  margin-right: 10px;
  position: relative;
  top: 3px;
}
.radio .cr {
  border-radius: 50%;
}
.checkbox .cr .cr-icon,
.radio .cr .cr-icon {
  position: absolute;
  font-size: 12px;
  line-height: 0;
  top: 50%;
  left: 1px;
}
.checkbox label input[type="checkbox"],
.radio label input[type="radio"] {
  display: none;
}
.checkbox label input[type="checkbox"] + .cr > .cr-icon,
.radio label input[type="radio"] + .cr > .cr-icon {
  -webkit-transform: scale(3) rotateZ(-20deg);
          transform: scale(3) rotateZ(-20deg);
  opacity: 0;
}
.checkbox label input[type="checkbox"]:checked + .cr > .cr-icon,
.radio label input[type="radio"]:checked + .cr > .cr-icon {
  -webkit-transform: scale(1) rotateZ(0deg);
          transform: scale(1) rotateZ(0deg);
  opacity: 1;
}
.checkbox label input[type="checkbox"]:disabled + .cr,
.radio label input[type="radio"]:disabled + .cr {
  opacity: 0.5;
}
.checkbox-right {
  margin-bottom: 15px;
}
.checkbox-right label {
  margin-right: 0;
  overflow: hidden;
}
.checkbox-right .cr {
  float: right;
  margin-right: 0;
  border: 3px solid #fff;
  border-radius: 50%;
  width: 22px;
  height: 22px;
  top: 0;
}
.checkbox-right .cr .cr-icon {
  width: 8px;
  height: 8px;
  background: #009444;
  border-radius: 50%;
  margin-top: -4px;
  left: 4px;
}
